import React, { Component } from 'react';

class Container extends Component{
	constructor(props) {
		super();
    }
    render(){
    	let len = this.props.data.length;
    	let style =['none','done','current'];
    	this.divs = [];
    	for(let i=0; i<len; i++) {
			let div = [],
				len_0 = this.props.data[0].length;
			for(let j=0; j<len_0; j++) {
				let jsx = <div className={style[this.props.data[i][j]]} style={{'top':(i*20 + 'px'),'left':((j*20) + 'px')}}></div>;
				div.push(jsx);
			}
			this.divs.push(div);
		}
    	return this.divs;
    }
}
export default Container;


