import React, { Component } from 'react';
import ReactPlayer from 'react-player'
import ReactDOM from 'react-dom'

class MyPlayer extends Component {
  	constructor(props) { 
        super(props);
        this.playURL = [
            require('../resource/mp3/2.mp3'),
            require('../resource/mp3/background.mp3'),
        ];
        this.playIndex = 0;
    } 
    click(ev){
        let player = ReactDOM.findDOMNode(this.refs["player"].player.player);
        if(ev.target == this.refs["pre"]){
            this.playIndex = (--this.playIndex)<0 ?this.playURL.length-1:this.playIndex;
            player.src = this.playURL[this.playIndex];
        } else if(ev.target == this.refs["next"]) {
            this.playIndex = (++this.playIndex) > this.playURL.length-1 ?0:this.playIndex;
            player.src = this.playURL[this.playIndex];
        }else if(ev.target == this.refs["stop"]) {
            let stopDOM = ReactDOM.findDOMNode(this.refs["stop"]);  
            if(stopDOM.getAttribute("data") === "0"){ 
                stopDOM.innerHTML = "播放";
                stopDOM.setAttribute("data",1);
                player.pause();
            } else {
                stopDOM.innerHTML = "暂停";
                stopDOM.setAttribute("data",0);
                player.play();
            }
            
        }

    }
    voiceClick(ev){
        let background = ReactDOM.findDOMNode(this.refs["background"]);
        // background.
    }
	render() {
		return (
		  <div>
		    <div className="myplayer" onClick={this.click.bind(this)}>
                <span ref ="pre">上一首</span>
                <span ref ="next">下一首</span>
                <span ref ="stop" data="0">暂停</span>
                <span ref ="voice" data="0" >音量:
                    <div ref="background" style={{width:"20px",height:"50px",background:"red"}} onClick={this.voiceClick.bind(this)}></div>
                </span>
            </div>
		    <ReactPlayer ref="player" width="100%" height="auto" url={this.playURL[this.playIndex]} playing controls="true"/>
		    
		  </div>
		);
	}
}

export default MyPlayer;


